import cell from "@lib/cell-lib";
import { Entity } from "../core/Entity";
import { MessageState } from "./MessageState";
import { AuthService } from "../auth/AuthService";

/**
 * 
 */
export class Message extends Entity {

    /**
     * 
     * @param {JSON} payload 
     */
    constructor(payload) {
        super();

        /**
         *  ID 
         * @type {number}
         */
        this.id = cell.Utils.generateSerialNumber();

        /**
         * 
         * @private
         * @type {string}
         */
        this.domain = AuthService.DOMAIN;

        /**
         * 
         * @type {JSON}
         */
        this.payload = {};

        if (undefined !== payload && !(payload instanceof File)) {
            this.payload = payload;
        }

        /**
         *  ID 
         * @type {number}
         */
        this.from = 0;

        /**
         *  ID 
         * @type {number}
         */
        this.to = 0;

        /**
         *  ID 
         * @type {number}
         */
        this.source = 0;

        /**
         * 
         * @type {number}
         */
        this.localTS = 0;

        /**
         * 
         * @type {number}
         */
        this.remoteTS = 0;

        /**
         * 
         * @type {number}
         * @see MessageState
         */
        this.state = MessageState.Unknown;
    }

    /**
     * 
     * @returns {string} 
     */
    getDomain() {
        return this.domain;
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getFrom() {
        return this.from;
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getTo() {
        return this.to;
    }

    /**
     *  ID 
     * @returns {number}
     */
    getSource() {
        return this.source;
    }

    /**
     * 
     * @returns {JSON} 
     */
    getPayload() {
        return this.payload;
    }

    /**
     * 
     * @returns {number} 
     */
    getRemoteTimestamp() {
        return this.remoteTS;
    }

    /**
     * 
     * @returns {number} 
     * @see MessageState
     */
    getState() {
        return this.state;
    }

    /**
     * 
     * @returns {number}  {@linkcode true}  {@linkcode false} 
     */
    isFromGroup() {
        return this.source > 0;
    }

    /**
     * 
     * @protected
     * @param {Message} src 
     */
    clone(src) {
        this.id = src.id;
        this.domain = src.domain;
        this.from = src.from;
        this.to = src.to;
        this.source = src.source;
        this.localTS = src.localTS;
        this.remoteTS = src.remoteTS;
        this.payload = src.payload;
        this.state = src.state;
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        json["id"] = this.id;
        json["domain"] = this.domain;
        json["from"] = this.from;
        json["to"] = this.to;
        json["source"] = this.source;
        json["lts"] = this.localTS;
        json["rts"] = this.remoteTS;
        json["state"] = this.state;
        json["payload"] = this.payload;
        return json;
    }

    /**
     *  JSON  {@linkcode Message} 
     * @param {JSON} json  JSON 
     * @returns {Message}  {@linkcode Message} 
     */
    static create(json) {
        let message = new Message();
        message.id = json.id;
        message.domain = json.domain;
        message.from = json.from;
        message.to = json.to;
        message.source = json.source;
        message.localTS = json.lts;
        message.remoteTS = json.rts;

        if (json.payload !== undefined) {
            message.payload = json.payload;
        }

        if (json.state !== undefined) {
            message.state = json.state;
        }
        return message;
    }
}
