import cell from "@lib/cell-lib";
import InDB from "indb";
import { Message } from "./Message";

/**
 * 
 */
export class MessagingStorage {

    constructor() {
        /**
         * 
         */
        this.domain = null;

        /**
         * 
         */
        this.db = null;

        /**
         * 
         */
        this.configStore = null;

        /**
         * 
         */
        this.messageStore = null;
    }

    /**
     * 
     * @param {string} domain 
     */
    open(domain) {
        if (null != this.db) {
            return;
        }

        cell.Logger.d('MessagingStorage', 'Open messaging storage : ' + domain);

        this.domain = domain;

        // 
        let options = {
            name: 'CubeMessaging-' + domain,
            version: 1,
            stores: [{
                name: 'message',
                keyPath: 'id',
                indexes: [{
                    name: 'id',
                    keyPath: 'id',
                    unique: true
                }, {
                    name: 'from',
                    keyPath: 'from',
                    unique: false
                }, {
                    name: 'to',
                    keyPath: 'to',
                    unique: false
                }, {
                    name: 'source',
                    keyPath: 'source',
                    unique: false
                }, {
                    name: 'rts',
                    keyPath: 'rts',
                    unique: false
                }]
            }, {
                name: 'config',
                keyPath: 'item',
                indexes: [{
                    name: 'item',
                    keyPath: 'item',
                    unique: true
                }]
            }]
        };

        this.db = new InDB(options);

        // 
        //this.db.connect();

        this.configStore = this.db.use('config');
        this.messageStore = this.db.use('message');
    }

    /**
     * 
     */
    close() {
        if (null == this.db) {
            return;
        }

        cell.Logger.d('MessagingStorage', 'Close messaging storage : ' + this.domain);

        this.db.close();
        this.db = null;
    }

    /**
     * 
     * @param {number} timestamp 
     * @returns {boolean} 
     */
    updateLastMessageTime(timestamp) {
        if (null == this.db) {
            return false;
        }

        (async () => {
            let data = {
                "item": "lastMessageTime",
                "value": timestamp
            };
            await this.configStore.put(data);
        })();
        return true;
    }

    /**
     * 
     * @param {function} handler ({@linkcode time}:number) 
     * @returns {boolean} 
     */
    queryLastMessageTime(handler) {
        if (null == this.db) {
            return false;
        }

        (async () => {
            let item = await this.configStore.get('lastMessageTime');
            if (undefined === item) {
                item = { value: 0 };
            }

            handler(item.value);
        })();
        return true;
    }

    /**
     * 
     * @param {Message} message 
     * @param {function} handler ({@linkcode message}:{@link Message}, {@linkcode contained}:boolean) 
     * @returns {boolean} 
     */
    containsMessage(message, handler) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let value = await this.messageStore.get(message.getId());
            handler(message, (undefined !== value && null != value));
        })();
        return true;
    }

    /**
     * 
     * @param {Message} message 
     * @returns {boolean} 
     */
    writeMessage(message) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let data = message.toJSON();
            delete data["domain"];
            await this.messageStore.put(data);
        })();
        return true;
    }

    /**
     * 
     * @param {number} beginning 
     * @param {function} handler ({@linkcode beginning}:number, {@linkcode result}:Array<{@link Message}>) 
     * @returns {boolean} 
     */
    readMessage(beginning, handler) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let result = await this.messageStore.select([
                { key: 'rts', value: beginning, compare: '>' }
            ]);
            let messages = [];
            for (let i = 0; i < result.length; ++i) {
                result[i].domain = this.domain;
                let message = Message.create(result[i]);
                messages.push(message);
            }
            handler(beginning, messages);
        })();
        return true;
    }

    /**
     * 
     * @param {number} contactId  ID 
     * @param {number} beginning 
     * @param {function} handler ({@linkcode contactId}:number, {@linkcode beginning}:number, {@linkcode result}:Array<{@link Message}>) 
     * @returns {boolean} 
     */
    readMessageWithContact(contactId, beginning, handler) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let result = await this.messageStore.select([
                { key: 'rts', value: beginning, compare: '>' },
                { key: 'source', value: 0, compare: '=' },
                { key: 'from', value: contactId, optional: true },
                { key: 'to', value: contactId, optional: true }
            ]);
            let messages = [];
            for (let i = 0; i < result.length; ++i) {
                result[i].domain = this.domain;
                let message = Message.create(result[i]);
                messages.push(message);
            }
            handler(contactId, beginning, messages);
        })();
        return true;
    }

    /**
     * 
     * @param {number} groupId  ID 
     * @param {number} beginning 
     * @param {function} handler ({@linkcode groupId}:number, {@linkcode beginning}:number, {@linkcode result}:Array<{@link Message}>) 
     * @returns {boolean} 
     */
    readMessageWithGroup(groupId, beginning, handler) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let result = await this.messageStore.select([
                { key: 'rts', value: beginning, compare: '>' },
                { key: 'source', value: groupId, optional: true }
            ]);
            let messages = [];
            for (let i = 0; i < result.length; ++i) {
                result[i].domain = this.domain;
                let message = Message.create(result[i]);
                messages.push(message);
            }
            handler(groupId, beginning, messages);
        })();
        return true;
    }

    /**
     * 
     * @param {Message} message 
     * @returns {boolean} 
     */
    updateMessage(message) {
        if (null == this.db) {
            return false;
        }

        (async ()=> {
            let data = message.toJSON();
            delete data["domain"];
            await this.messageStore.put(data);
        })();
        return true;
    }
}
